<template>
  <div id="app">
    <demonstration
        title="Message "
        tag="h2"
        description=" Notification "
        :show-content="false">
    </demonstration>
    <!--  -->
    <demonstration
        title=""
        description="3 "
        :highlight="sourcecodeA">
      <template slot="source">
        <vm-button :plain="true" @click="open"></vm-button>
        <vm-button :plain="true" @click="openVn">VNode</vm-button>
      </template>
      <template slot="explanation">
         <code>options</code>  <code>message</code> 
         <code>Message</code>  <code>duration</code> <code>0</code>
        <code>duration</code>  <code>Number</code> <code>3000</code>
      </template>
    </demonstration>
    <!--  -->
    <demonstration
        title=""
        description=""
        :highlight="sourcecodeB">
      <template slot="source">
        <vm-button :plain="true" @click="open2"></vm-button>
        <vm-button :plain="true" @click="open3"></vm-button>
        <vm-button :plain="true" @click="open"></vm-button>
        <vm-button :plain="true" @click="open4"></vm-button>
      </template>
      <template slot="explanation">
        <code>Message</code>  <code>type</code>  <code>info</code>
         <code>message</code>  <code>Message</code>  <code>type</code>
         <code>type</code>  <code>open4</code> 
      </template>
    </demonstration>
    <!--  -->
    <demonstration
        title=""
        description="Loading "
        :highlight="sourcecodeC">
      <template slot="source">
        <vm-button :plain="true" @click="openLoading"></vm-button>
      </template>
      <template slot="explanation">
         <code>Message</code> 
      </template>
    </demonstration>
    <demonstration
        title=""
        description=""
        :highlight="sourcecodeD">
      <template slot="source">
        <vm-button :plain="true" @click="open5"></vm-button>
        <vm-button :plain="true" @click="open6"></vm-button>
        <vm-button :plain="true" @click="open7"></vm-button>
        <vm-button :plain="true" @click="open8"></vm-button>
      </template>
      <template slot="explanation">
         <code>Message</code>  <code>Message</code> <code>showClose</code> 
      </template>
    </demonstration>
    <demonstration
        title=""
        :show-content="false"
        :code="sourcecodeF">
      <template slot="description">
         <code>Message</code>   Vue.prototype  $message vue instance  Message
      </template>
      <templalte>
        <div class="vd-demo__desc">
          <ul>
            <li><code>this.$message(options)</code></li>
            <li><code>this.$message.info(options)</code></li>
            <li><code>this.$message.success(options)</code></li>
            <li><code>this.$message.warning(options)</code></li>
            <li><code>this.$message.error(options)</code></li>
            <li><code>this.$message.loading(options)</code></li>
          </ul>
        </div>
      </templalte>
    </demonstration>
    <demonstration
        title=""
        :show-content="false"
        :code="sourcecodeE">
      <template slot="description">
         <code>Message</code>
      </template>
      <template>
        <div class="vd-demo__desc">
           <code>Message(options)</code> <code>type</code>  <code>Message.success(options)</code>  <code>Message.closeAll()</code>
          
          <ul>
            <li><code>Message(options)</code></li>
            <li><code>Message.info(options)</code></li>
            <li><code>Message.success(options)</code></li>
            <li><code>Message.warning(options)</code></li>
            <li><code>Message.error(options)</code></li>
            <li><code>Message.loading(options)</code></li>
          </ul>
        </div>
      </template>
    </demonstration>
    <demonstration
        title="Options"
        :show-content="false">
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>zIndex <i class="vd-demo__new"></i></td>
            <td></td>
            <td>number</td>
            <td></td>
            <td>2000</td>
          </tr>
          <tr>
            <td>message</td>
            <td></td>
            <td>string / VNode</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>type</td>
            <td></td>
            <td>string</td>
            <td>success/warning/info/error/loading</td>
            <td>info</td>
          </tr>
          <tr>
            <td>iconClass</td>
            <td> <code>type</code></td>
            <td>string</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>customClass</td>
            <td></td>
            <td>string</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>duration</td>
            <td>,  0 </td>
            <td>number</td>
            <td></td>
            <td>3000</td>
          </tr>
          <tr>
            <td>showClose</td>
            <td></td>
            <td>boolean</td>
            <td></td>
            <td>false</td>
          </tr>
          <tr>
            <td>onClose</td>
            <td>,  message </td>
            <td>function</td>
            <td></td>
            <td></td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
    <demonstration
        title=""
        :show-content="false">
      <template slot="description">
         <code>Message</code>  <code>this.$message</code>  <code>Message</code> 
         <code>close</code> 
      </template>
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>close</td>
            <td> Message</td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
    <demonstration
        :show-content="false"
        :code="sourcecodeG">
      <template slot="title">
         <i class="vd-demo__new"></i>
      </template>
      <template slot="description">
         <code>Message.config(options) </code> 
      </template>
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>top</td>
            <td></td>
            <td>number</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>duration</td>
            <td>,  0 </td>
            <td>number</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>zIndex</td>
            <td></td>
            <td>number</td>
            <td></td>
            <td></td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
  </div>
</template>

<script>
  import {sourcecodeA, sourcecodeB, sourcecodeC, sourcecodeD, sourcecodeE, sourcecodeF, sourcecodeG} from './template'
  export default {
    data () {
      return {
        sourcecodeA,
        sourcecodeB,
        sourcecodeC,
        sourcecodeD,
        sourcecodeE,
        sourcecodeF,
        sourcecodeG
      }
    },
    methods: {
      open() {
        this.$message('');
      },

      openLoading () {
        this.message = this.$message({
          type: 'loading',
          message: '',
          duration: 0
        })
        setTimeout(() => {
          this.message.close()
        }, 3000)
      },

      open2() {
        this.$message({
          message: '',
          type: 'success'
        });
      },

      open3() {
        this.$message({
          message: '',
          type: 'warning'
        });
      },

      open4() {
        this.$message.error('');
      },

      open5() {
        this.$message({
          showClose: true,
          message: ''
        });
      },

      open6() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
      },

      open7() {
        this.$message({
          showClose: true,
          message: '',
          type: 'warning'
        });
      },

      open8() {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      },

      openVn() {
        const h = this.$createElement;
        this.$message({
          message: h('p', {
            style: {
              display: 'inline-block',
              margin: 0
            }
          }, [
            h('span', null, ' '),
            h('i', {style: 'color: teal'}, 'VNode')
          ])
        });
      }
    }
  }
</script>

<style>
  #app {
    margin: 60px auto;
  }
</style>



