export const sourcecodeA = `<template>
  <vm-button :plain="true" @click="open"></vm-button>
  <vm-button :plain="true" @click="openVn">VNode</vm-button>
</template>

<script >
  export default {
    methods: {
      open() {
        this.$message('');
      },
      
      openVn() {
        const h = this.$createElement;
        this.$message({
          message: h('p', {
            style: {
              display: 'inline-block',
              margin: 0
            }
          }, [
            h('span', null, ' '),
            h('i', {style: 'color: teal'}, 'VNode')
          ])
        });
      }
    }
  }
</script>`

export const sourcecodeB = `<template>
  <vm-button :plain="true" @click="open2"></vm-button>
  <vm-button :plain="true" @click="open3"></vm-button>
  <vm-button :plain="true" @click="open"></vm-button>
  <vm-button :plain="true" @click="open4"></vm-button>
</template>
<script>
  export default {
    methods: {
      open() {
        this.$message('');
      },
      open2() {
        this.$message({
          message: '',
          type: 'success'
        });
      },

      open3() {
        this.$message({
          message: '',
          type: 'warning'
        });
      },

      open4() {
        this.$message.error('');
      }
    }
  }
</script>`

export const sourcecodeC = `<template>
  <vm-button :plain="true" @click="openLoading"></vm-button>
</template>

<script >
  export default {
    methods: {
     openLoading () {
        this.message = this.$message({
          type: 'loading',
          message: '',
          duration: 0
        })
        setTimeout(() => {
          this.message.close()
        }, 3000)
      }
    }
  }
</script>`

export const sourcecodeD = `<template>
  <vm-button :plain="true" @click="open5"></vm-button>
  <vm-button :plain="true" @click="open6"></vm-button>
  <vm-button :plain="true" @click="open7"></vm-button>
  <vm-button :plain="true" @click="open8"></vm-button>
</template>

<script>
  export default {
    methods: {
      open5() {
        this.$message({
          showClose: true,
          message: ''
        });
      },

      open6() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
      },

      open7() {
        this.$message({
          showClose: true,
          message: '',
          type: 'warning'
        });
      },

      open8() {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    }
  }
</script>`

export const sourcecodeE = `import Message from 'vue-multiple-message'`

export const sourcecodeF = `import Vue from 'vue'
import Message from 'vue-multiple-message'
Vue.prototype.$message = Message`

export const sourcecodeG = `Messge.config({
  top: 20,
  duration: 3000,
  zIndex: 2000
})`


